@font-face {
    font-family: myFirstFont;
    src: url(digital.ttf);
}

.clock-unit {
    font-family: myFirstFont;
    color: darkgreen;
    font-size: 64px;
}

dot {
    position: relative;
    width: 2.5vmin;
    height: 2.5vmin;
    background-color: black;
    border-radius: 50%;
    align-self: center;
    left: 1vmin;
    z-index: 2;
}