@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");

body {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: green;
}

.screen {
  height: fit-content;
  width: fit-content;
  border-radius: 10px;
  background-color: cadetblue;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 50px;
  font-size: 40px;
  font-family: "Roboto", sans-serif;
}

.toggle-switch {
  position: absolute;
  right: 10px;
  top: 0px;
}