.AnalogClock {
    height: 60vmin;
    width: 60vmin;
    background-size: cover;
    display: flex;
    justify-content: center;
}

.clock-hand {
    position: relative;
    background-color: black;
    transform-origin: bottom;
    right: 0%;
}

.hour-hand {
    width: 0.8vmin;
    height: 30%;
    top: 19.5%;
    border-radius: 50em;
}

.minute-hand {
    width: 0.4vmin;
    height: 30%;
    top: 20.1%;
    right: 0.5vmin;
}

.second-hand {
    width: 0.3vmin;
    height: 35%;
    top: 15.05%;
    right: 1.45vmin;
}